.room-inf-form {
    position: absolute;
    width: 380px;
    height: 511px;
    left: 560px;
    top: 150px;

    background: #FFFFFF;
    border: 1px solid rgba(0, 0, 0, 0.12);
    box-sizing: border-box;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2);
    border-radius: 4px;

    button {
        position: absolute;
        display: flex;
        width: 320px;
        height: 44px;
        left: 30px;
        top: 437px;

        cursor: pointer;

        background: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%);
        border-radius: 22px;
        border-style: none;

        p {
            width: 280px;
            height: 15px;
            margin-top: 14px;
            margin-left: 34px;

            font-family: Conv_Montserrat-Regular;
            font-style: normal;
            font-weight: bold;
            font-size: 11px;
            line-height: 15px;

            text-align: center;
            text-transform: uppercase;

            color: #FFFFFF;
        }

        .arrow {
            width: 44px;
            height: 34px;
            margin-top: 9px;
            margin-right: 4px;

            font-family: 'Material Icons';
            font-weight: normal;
            font-style: normal;
            font-size: 24px;
            line-height: 24px;
            text-transform: none;
            letter-spacing: normal;
            word-wrap: normal;
            white-space: nowrap;
            direction: ltr;
            text-align: end;

            color: #FFFFFF;

            /* Support for all WebKit browsers. */
            -webkit-font-smoothing: antialiased;
            /* Support for Safari and Chrome. */
            text-rendering: optimizeLegibility;
            /* Support for Firefox. */
            -moz-osx-font-smoothing: grayscale;
        }
    }
}